// WebGLAnimation.js

import alfrid, { GL } from "alfrid";

import Assets from "./Assets";
import AssetsLoader from "assets-loader";
import Config from "./Config";
import Emitter from "events";
import ViewGradient from "./ViewGradient";
import assets from "./asset-list";
import objectAssign from "object-assign";

class WebGLAnimation extends Emitter {
  constructor(mConfig = {}) {
    super();

    this._isPlaying = true;
    objectAssign(Config, mConfig);

    this.isSupported = true;

    if (this.isSupported) {
      this.canvas = document.createElement("canvas");
      GL.init(this.canvas, { preserveDrawingBuffer: !!window.isDevelopment });
      this._loadAssets(mConfig.ASSETS_PATH || "assets/");

      if (mConfig.container) {
        mConfig.container.appendChild(this.canvas);
      }
    } else {
      setTimeout(() => {
        this.emit("onError", {
          error: "Mobile not supported for this experiment",
        });
      }, 10);
    }
  }

  _loadAssets(assetsPath) {
    if (!this.isSupported) return;
    const assetsAry = assets.map((oAsset) => {
      oAsset.url = oAsset.url.replace("assets/", assetsPath);
      return oAsset;
    });

    if (assetsAry.length === 0) {
      setTimeout(() => {
        this._onAssetsLoaded();
      }, 1000 / 60);
    }

    const loader = new AssetsLoader({
      assets: assetsAry,
    })
      .on("error", (error) => {
        console.log("Error :", error);
      })
      .on("progress", (p) => {
        this.emit("onProgress", { percent: p });
      })
      .on("complete", (o) => this._onAssetsLoaded(o))
      .start();
  }

  _onAssetsLoaded(o) {
    if (!this.isSupported) return;
    Assets.init(o);
    this._initScenes();

    this.emit("onLoaded");
  }

  onToggleSound() {
    if (!this.isSupported) return;
    console.log("toggle sound");
  }

  start() {
    if (!this.isSupported) return;
    alfrid.Scheduler.addEF(() => this.render());
  }

  play() {
    if (!this.isSupported) return;
    this._isPlaying = true;
  }

  pause() {
    if (!this.isSupported) return;
    this._isPlaying = false;
  }

  _initScenes() {
    if (!this.isSupported) return;
    // camera
    const RAD = Math.PI / 180;
    this.camera = new alfrid.CameraPerspective();
    this.camera.setPerspective(90 * RAD, GL.aspectRatio, 0.1, 100);
    this.orbitalControl = new alfrid.OrbitalControl(
      this.camera,
      Config.container || window,
      15
    );
    this.orbitalControl.lock(true);
    // this.orbitalControl.rx.value = this.orbitalControl.ry.value = 0.3;

    this._bAxis = new alfrid.BatchAxis();
    this._bDots = new alfrid.BatchDotsPlane();
    this._vGradient = new ViewGradient(this);

    this.resize(window.innerWidth, window.innerHeight);
  }

  render() {
    if (!this.isSupported) return;
    if (!this._isPlaying) {
      return;
    }

    GL.viewport(0, 0, GL.width, GL.height);
    GL.clear(0, 0, 0, 1);

    GL.setMatrices(this.camera);
    // this._bAxis.draw();
    // this._bDots.draw();

    this._vGradient.render();
  }

  resize(w, h) {
    if (!this.isSupported) return;
    GL.setSize(w, h);
    this.camera.setAspectRatio(GL.aspectRatio);
    this._vGradient.resize(w, h);
  }
}

export default WebGLAnimation;



