import alfrid, { GL } from "alfrid";

import Config from "./Config";
import Plane from "./Plane";
import { fitXY } from "utils";
import fs from "shaders/gradient.frag";
import gsap from "gsap";
import vs from "shaders/gradient.vert";

// var R = function(x, y, t) {
//   return( Math.floor(192 + 64*Math.cos( (x*x-y*y)/300 + t )) );
// }

// var G = function(x, y, t) {
//   return( Math.floor(192 + 64*Math.sin( (x*x*Math.cos(t/4)+y*y*Math.sin(t/3))/300 ) ) );
// }

// var B = function(x, y, t) {
//   return( Math.floor(192 + 64*Math.sin( 5*Math.sin(t/9) + ((x-100)*(x-100)+(y-100)*(y-100))/1100) ));

const colors = {
  pig: [243, 176, 176],
  leaf: [67, 177, 121],
  peach: [255, 127, 112],
  yellow: [255, 209, 77],
  sky: [58, 201, 210],
  red: [224, 68, 68],
  teal: [103, 211, 204],
  sunset: [239, 117, 142],
};

const colorsRGB = Object.keys(colors).map((key) => {
  return colors[key];
});

export default class ViewGradient {
  constructor(scene) {
    this.transform = new alfrid.Object3D();
    this.scene = scene;
    this.time = 0;
    this.randomX = 0;
    this.paused = false;
    this.randomY = 0;
    this.mesh = new alfrid.Geom.plane(1, 1, 20);
    this._draw = new alfrid.Draw(GL).setMesh(this.mesh).useProgram(vs, fs);

    this.lineSpeed = -0.002;

    this.displayValue = 1;

    this.color1 = []; //colors.sky;
    this.color2 = []; //colors.yellow;
    this.color3 = []; //colors.peach;
    this.color4 = []; //colors.pig;

    this.colors = [this.color1, this.color2, this.color3, this.color4];
    this.freeColours = [...colorsRGB];

    this.setColors();

    setTimeout(() => {
      this.tweenColors();
    }, 1000);
  }

  showNoise1() {
    this.displayValue = 0.2;
  }

  showNoise2() {
    this.displayValue = 0.5;
  }

  showLines() {
    this.displayValue = 0.7;
  }

  showGradient() {
    this.displayValue = 1;
  }

  togglePause() {
    this.paused = !this.paused;
  }

  setColors() {
    this.colors.map((colour) => {
      const randomIndex = Math.floor(Math.random() * this.freeColours.length);
      const colourTarget = this.freeColours[randomIndex];
      this.freeColours.splice(randomIndex, 1);

      colour[0] = colourTarget[0];
      colour[1] = colourTarget[1];
      colour[2] = colourTarget[2];
    });
  }
  tweenColors() {
    this.colors.map((colour) => {
      this.tweenColor(colour);
    });
  }

  tweenColor(colour) {
    this.freeColours.push([...colour]);

    const randomIndex = Math.floor(Math.random() * this.freeColours.length);
    const colourTarget = this.freeColours[randomIndex];

    gsap.to(colour, 2, {
      delay: 1,
      0: colourTarget[0],
      1: colourTarget[1],
      2: colourTarget[2],
      ease: "none",
      onComplete: () => {
        this.tweenColor(colour);
      },
    });
  }

  render() {
    GL.rotate(this.transform.matrix);

    if (!this.paused) {
      this.time++;
    }

    const blur = Math.max(0.001, Math.sin(this.time / 100) * 0.1);

    Config.lineX += this.paused ? 0 : this.lineSpeed;
    if (Config.lineX < -1.75) {
      Config.lineX = 1.75;
      this.lineSpeed = (Math.random() * 0.002 + 0.005) * -1;
      Config.rotate = 0.1 + Math.random() * Math.PI * 0.8;
    }

    this._draw
      .uniform("uRandomisePosition", "vec2", [this.randomX, this.randomY])
      .uniform("uTime", "float", this.time / 100)
      .uniform("uLineCurve", "float", Config.lineCurve)
      .uniform("uLineX", "float", Config.lineX)
      .uniform("uLineBlur", "float", blur)
      .uniform("uColor1", "vec3", this.color1)
      .uniform("uColor2", "vec3", this.color2)
      .uniform("uColor3", "vec3", this.color3)
      .uniform("uColor4", "vec3", this.color4)
      .uniform("uScale", "float", Config.scale)
      .uniform("uSpeed", "float", Config.speed)
      .uniform("uDisplayValue", "float", this.displayValue)
      .uniform("uFadeInLine", "float", Config.fadeInLine)
      // .uniform("uLineBlur", "float", Config.lineBlur)
      .uniform("uRotate", "float", Config.rotate)
      .draw();
  }

  resize(w, h) {
    const { scaleX, scaleY } = fitXY(
      w,
      h,
      0,
      this.scene.camera,
      this.scene.orbitalControl
    );

    this.transform.scaleX = scaleX;
    this.transform.scaleY = scaleY;
  }
}



